import axios from "../libs/api.request.js";

// 
export const login = (account) => {
    let data = account
    return axios.requestVerification({//
        url: '/Home/Qbw/user_login',
        data,
        method: 'post'
    })
};

export const register = (data) => {
    return axios.requestVerification({
        url: '/Home/Qbw/register',//
        data,
        method: 'post'
    })
};
// 
export const reset_opwd = (data) => {
    return axios.requestVerification({
        url: '/home/qbw/reset_opwd',//
        data,
        method: 'post'
    })
};
export const userInfo = (data) => {
    return axios.request({
        url: '/Home/Qbw/user_info',
        params: data,
        method: 'get'
    })
};
export const sendMsg = (data) => {
    return axios.requestVerification({
        url: '/Home/Qbw/send_sms',
        params: data,
        method: 'get'
    })
};
export const sendEmailMsg = (data) => {
    return axios.request({
        url: '/Home/Mail/send_mail',
        data,
        method: 'post'
    })
};
// 
