/*   
**    
** btc_usdt, BTC/USDT, btc
*/ 
let fn = (code) => {
    if (code == "") return ""
    var _code = code.toUpperCase()
    var name;
    if (_code.indexOf("_") > -1 || _code.indexOf("/") > -1) {
        name = _code.split('_')[1] ? _code.split('_')[0] : _code.split('/')[0]
    } else {
        name = _code
    }
    // base64 http://imgbase64.duoshitong.com/
    var obj = {
        'BTC': 'data:image/png;base64,',
        'ADA': 'data:image/png;base64,',
        'BCH': 'data:image/png;base64,',
        'EOS': 'data:image/png;base64,',
        'ETC': 'data:image/png;base64,',
        'ETH': 'data:image/png;base64,',
        'LTC': 'data:image/png;base64,',
        'QTUM': 'data:image/png;base64,',
        'XMR': 'data:image/png;base64,',
        'DASH': 'data:image/png;base64,',
        'HT': 'data:image/png;base64,',
        'TRX': 'data:image/png;base64,',
        'XRP': 'data:image/png;base64,',
        'other': '',
    }
    return obj[name] ? obj[name] : obj.other
}
export default fn ;


